@import '../../common/styles/variables';

.about_wrapper {
  opacity: 0;
  animation: fade .5s ease;
  animation-fill-mode: forwards;
  position: relative;
  min-height: 100vh;
  background: url('../../common/images/background.webp') no-repeat center center;
  background-size: cover;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 100%;

  @keyframes fade {
    from {
      opacity: 0
    }
    to {
      opacity: 1
    }
  }

  @media (max-width: 1025px) {
    display: none;
  }

  &:before {
    content: '';
    background-size: cover;
    background-color: rgba(0, 0, 0, .85);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
  }

  .wrapper {
    display: flex;
    align-items: center;
    width: 65%;
    gap: 50px;
    z-index: 1;

    @media (max-width: 950px) {
      display: none;
    }

    .photoContainer {
      position: relative;
      max-width: 550px;
      border: 10px solid rgba(255, 255, 255, .1);
      border-radius: 50%;

      .photo {
        position: relative;
        width: 300px;
        border-radius: 50%;
        object-fit: cover;
      }
    }

    .textContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-family: $primaryFont;
      color: #eee;

      .btnWrapper {
        margin-top: 20px;
      }

      .textWrapper {
        .greetings {
          font-weight: 500;
          font-size: 19px;
          font-family: $secondaryFont;
        }

        .nameContainer {
          font-size: 42px;
          line-height: 42px;
          margin: 19px 0 10px;
          text-transform: uppercase;
          font-family: $secondaryFont;
          font-weight: bold;

          @media (min-width: 1025px) and (max-width: 1250px) {
            min-width: 410px;
          }

          .myName {
            color: $secondaryTextColor;
          }
        }

        .aboutText {
          display: block;
          font-size: 18px;
          line-height: 32px;
          font-weight: 400;
          font-family: $secondaryFont;
          margin-bottom: 0;
        }
      }
    }
  }
}










